import { isAuth } from '@/utils'
import { Redirect, Route,  } from 'react-router-dom'

// https://reactrouter.com/web/example/auth-workflow
// https://stackoverflow.com/a/64307442/15443637


/**
 * AuthRoute: , token token  
 */
const AuthRoute = ({ component: Component, ...rest }) => {   ///  component & path
  // const loc = useLocation()
  return (
    <Route
      {...rest}  //  exact 
      render={props => {
        // console.log(props)
        // isAuth    : () => !!getTokens().token
        // isAuth()  : false 
        //  !isAuth()false  token()  ||   !isAuth()true  
        if (!isAuth()) {   
          return (
            <Redirect to={
              {
                pathname: '/login',
                state: {
                  from: props.location.pathname 
                }
              }
            } />
          )
        }else{
          return <Component {...props} />   /**    */ 
        }
      }}
    />
  )
}

export { AuthRoute }
