import Icon from '@/components/Icon'
import Input from '@/components/Input'
import NavBar from '@/components/NavBar'
import { getTokens } from '@/utils'
import { Toast } from 'antd-mobile'
import classnames from 'classnames'
import { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import io from 'socket.io-client'
import styles from './index.module.scss'
// let client = null;   client   



/**

==>
 * 1.NavBar()
 * 2.--> --> --> 
 * 3.list & list
 * 4.Input   ?
==>
 * 5.useEffect    ?socketio socketio
 * 6.InputValue, onChange, onKeyDown
 * 7.useState[value, setValue]  useState  .  setValue
 * 8.useState[list,  setList ]  Hooks  Hooks
 * 9.useSelectorreduxaxiosredux
 * 10.useRefsocketiosocketIO.current   emituseRef
 * 11.useEffectuseState--> Hooks?
 * 12.useEffect: https://www.jianshu.com/p/a619861445d0
 * 13.useStateuseEffect: http://ourjs.com/detail/kbbgiz4hwsih
 * 14.useRef???
 */
const Chat = () => {
  const history = useHistory()
  
  const profile = useSelector(state => state.profile.user)// redux
  const [value, setValue] = useState([ ])
  // -- --> list--> setList--> useState
  const [list, setList] = useState([
    { name: 'xz', msg: '' },
    { name: 'self', msg: '!' }
  ])
  const socketIO = useRef(null)  ///----- useEffect  client   useRef  socketIO
  const chatListRef = useRef(null)

  // , socketio,   && ()
  useEffect(() => {
    // dispatch(getUser())//-------    

    const socketio = io('http://toutiao.itheima.net', {
      query: {
        token: getTokens().token
      },
      transports: ['websocket']
    })
    socketio.on('connect', () => {
      setList(list => [
        ...list,
        { name: 'xz', msg: 'Hello!' }
      ])
      Toast.info(' !', 0.5)
    })
    
    socketio.on('message', data => {
      setList(list => [
        ...list, 
        { name: 'xz', msg: data.msg }
      ])
    })
 
    socketIO.current = socketio

    // !!!
    // 
    //  socketio
    return () => socketio.close()
  }, [])   //-------------------------- 


  useEffect(() => {
    // chatListRef.current.scrollTop = chatListRef.current.scrollHeight
    chatListRef.current.scrollTop = chatListRef.current.scrollHeight - chatListRef.current.offsetHeight   
  }, [list]) //---->list,,,list  


  //   :  1.    2.
  const onSend = e => {
    // useStateValue
    // console.log('value iS', value)  // value
    // console.log('e iS', e) // e &  onKeyDown
    if (!value) return
    if (e.keyCode === 13) {
      socketIO.current.emit('message', { 
        msg: value, 
        timestamp: Date.now()
       })
      // console.log(...list)///--------->    ...list  setList 
      setList([
        ...list, 
        { name: 'self', msg: value }
      ])
      setValue('') ///--- 
    }
  }




  return (
    <div className={styles.root}>
      <NavBar className="fixed-header" onLeftClick={() => history.go(-1)}></NavBar>

      {/*   self ? user    */}
      <div className="chat-list" ref={chatListRef}>
        {/* 
            <div className='chat-item'> 
              <Icon type='iconbtn_xiaozhitongxue' />
              <div className='message'>!</div>
            </div>
       */}
        {
          // item: {name: 'xz', msg: ''} | {name: 'self', msg: '!'}   index: 0, 1, 2, 3, 4, 5
          list.map((item, index) => (
            // div div  
            <div
              className={classnames( 'chat-item', item.name === 'self' ? 'user' : '' )}
              key={index}
            >
              {
                item.name === 'self' ? 
                (<img src={ profile.photo || 'http://toutiao.itheima.net/images/user_head.jpg' } alt="" />) : (<Icon type="iconbtn_xiaozhitongxue" />)
              }
              <div className="message">{item.msg}</div>
            </div>
          ))
        }
      </div>

{/*  */}
      <div className="input-footer">
        <Input
          className="no-border"
          placeholder=""
          onKeyDown={onSend}
          value={value}
          onChange={e => setValue(e.target.value)}
        />
        <Icon type="iconbianji" />
      </div>
    </div>
  )
}

export default Chat
