import Icon from '@/components/Icon'
import { addChannel, deleteChannel, getRecommendChannel } from '@/store/actions'
import { isAuth } from '@/utils'
import classnames from 'classnames'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styles from './index.module.scss'



const Channels = ({ userChannles = [], tabActiveIndex, onClose, onChange, onChannelClick }) => {
 
  const dispatch = useDispatch()
  const channelsList = useSelector(state => state.home.recommendChannel)    //----  redux
  const [isEdit, setIsEdit] = useState(false)

  const isLogin = isAuth()

  useEffect(() => {
    if (channelsList.length === 0) {
      dispatch(getRecommendChannel(userChannles))
    }
  }, [dispatch, userChannles, channelsList.length])

  const onEdit = () => setIsEdit(!isEdit)

  // 
  const onDeleteChannel = async data => {

    // if(userChannles.length <= 4){
    //   Toask.info('4', 1)
    //   return 
    // }
    // console.log(' iS', data)
    const newTabs = userChannles.filter(item => item.id !== data.id)
    onChange(newTabs)

    // 
    dispatch(deleteChannel(data, isLogin))



    //     i    onClick  i
      // 1. i  index , 0 
      // 2. i  index,  i - 1 
      // 3. i  index, 
      // console.log('i', i)
      // console.log('index', index)
    // if(i < index) {
    //   onChange(i - 1)
    // }else if(i === index){
    //   onChange(0)
    // }else{
    //   onChange(i)
    // }



  }

  
  // 
  //   ?  
  const onAddChannel = async data => {
    const newTabs = [...userChannles, data]
    onChange(newTabs)

    // 
    dispatch(addChannel(data, isLogin))
  }

  // 
  const onChannelItemClick = index => {

    setIsEdit(false)
    onClose()
    onChannelClick(index)
  }








  return (
    <div className={styles.root}>
      <div className="channel-header">
        <Icon type="iconbtn_channel_close" onClick={onClose} />
      </div>
      <div className="channel-content">
        <div className={classnames('channel-item', isEdit ? 'edit' : '')}>
          <div className="channel-item-header">
            <span className="channel-item-title"></span>
            <span className="channel-item-title-extra">
              {isEdit ? '' : ''}
            </span>
            <span className="channel-item-edit" onClick={onEdit}>
              {isEdit ? '' : ''}
            </span>
          </div>
          <div className="channel-list">
            {userChannles.map((item, index) => (
              <span
                key={item.id}
                className={classnames(
                  'channel-list-item',
                  index === tabActiveIndex ? 'selected' : ''
                )}
                onClick={() => onChannelItemClick(index)}
              >
                {item.title}
                {/*   */}
                {/* {item.id !== 0 && (<Icon>  </Icon>)} */}
                <Icon
                  type="iconbtn_tag_close"
                  onClick={e => {
                    e.stopPropagation()

                    onDeleteChannel(item)   /// item 
                  }}
                />
              </span>
            ))}

            {/* <span className="channel-list-item"></span> */}
          </div>
        </div>

        <div className="channel-item">
          <div className="channel-item-header">
            <span className="channel-item-title"></span>
            <span className="channel-item-title-extra"></span>
            {/* <span className="channel-item-edit"></span> */}
          </div>
          <div className="channel-list">
            {channelsList.map(item => (
              <span
                key={item.id}
                className="channel-list-item"
                onClick={() => onAddChannel(item)}
              >
                + {item.title}
              </span>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Channels
