import NavBar from '@/components/NavBar'
import {  getUserProfile,  logout, updateAvatar, updateProfile } from '@/store/actions'
import { DatePicker, Drawer, List, Modal, Toast } from 'antd-mobile'
import classnames from 'classnames'
import { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import EditInput from './components/EditInput'
import EditList from './components/EditList'
import styles from './index.module.scss'

const Item = List.Item
const alert = Modal.alert


/**
 * 
==>
 * 1.Edit->indexjs <Link to="/profile/edit"></Link>    (...)   &&  history
==>
 * 2.List->Item    
 * 3.Item     | User | >    DatePickerItem
 * 4.antd-mobile(....)
 * 5.antd-mobilev2   v5
 * 6.EditListEditInput
==>
 * 7.Item: setOpenList({ visible: true, type: 'avatar' })  ?
 * 8.EditListEditInput
 * 9./--> 1. 2.FormData 3.dispatch 4.redux  5.setOpenListfalse
 * 10.--> 1.onChangedate 2.dispatch  3.redux 4.useSelector
 * 11.  EditList 
 * 12.  EditInput 
 * 13.EditInput  InputNavBarTextarea
 */
const ProfileEdit = () => {
  const dispatch = useDispatch()

  // reduxprofile
  const userInfo = useSelector(state => state.profile.userProfile)
  // console.log('userInfo iS', userInfo) 
  const history = useHistory()
  const fileRef = useRef() //---> useRef input

  // 
  const [openInput, setOpenInput] = useState({
    visible: false,
    type: 'input'
  })
  // 
  const [openList, setOpenList] = useState({
    visible: false,
    type: 'gender'
  })

  //  
  useEffect(() => {
    dispatch(getUserProfile())///--------->: birthday, gender, id, intro, name, mobile, photo
  }, [dispatch])

  /**-->setOpenInputfalse  */
  const onCloseOpenInput = () => {
    setOpenInput({
      visible: false,
      type: ''
    })
  }
  /**-->setOpenListfalse  */
  const onCloseOpenList = () => {
    setOpenList({
      visible: false,
      type: ''
    })
  }

  // : 
  const openListConfig = openList.type === 'avatar' ? 
              [{ title: '', onClick: () => { fileRef.current.click() } },
              { title: '', onClick: () => {fileRef.current.click()} }] 
              : 
              [{ title: '', onClick: () => { dispatch(updateProfile('gender', 0)); setOpenList({ visible: false, type: ''})  }},
              {title: '', onClick: async () => { await dispatch(updateProfile('gender', 1)); setOpenList({ visible: false, type: ''})  }}]


  // : 
  const openInputConfig = openInput.type === 'name' ? {title:'',type:'name',value:userInfo.name}: { title:'',type:'intro',value:userInfo.intro }
      

  /**/--> 1. 2.FormData 3.dispatch 4.redux  5.setOpenListfalse */
  const onUpdateAvatar = e => {
    const avatar = e.target.files[0]
    const formData = new FormData()
    formData.append('photo', avatar)

    dispatch(updateAvatar(formData))
    Toast.success('', 1)

    setOpenList({
      visible: false,
      type: ''
    })
  }

  /**--> 1.onChangedate 2.dispatch  3.redux 4.useSelector */
  const onBirthdayChange = value => {
    const date = `${value.getFullYear()}-${ value.getMonth() + 1  }-${value.getDate()}`
    dispatch(updateProfile('birthday', date))
  }

  /**--> 1.EditInput  2.dispatch 3.redux 4.useSelector 5.setOpenInputfalse  */
  const onCommit = (name, value) => {
    dispatch(updateProfile(name, value))
    setOpenInput({  visible: false, type: '' })
  }


  /**--> 1. 2.token(redux) 3. */
  const onLogout = () => {
    // Modal.alert('', '', [ {''}, {''} ])    
    // 1. 2.token(redux) 3.
    alert('', '', [
      {
        text: ''
      },
      {
        style: { color: '#FC6627' },
        text: '',
        onPress: () => {
          dispatch(logout())
          history.replace('/login'); 
          Toast.info('!', 1)
        }
      }
    ])
    
  }






  // 
  
  return (
    <div className={styles.root}>
      <div className="content">
        <NavBar onLeftClick={() => history.go(-1)}></NavBar>

{/* List.Item: https://mobile.ant.design/zh/components/list */}
        <div className="wrapper">
          <List className="profile-list">
{/* List.Item */}
            <Item
              arrow="horizontal"  //----> :    horizontal   >
              //   	ReactNode
              extra={
                <span className="avatar-wrapper">
                  <img src={userInfo.photo} alt="" />
                </span>         
              }
              onClick={() => setOpenList({ visible: true, type: 'avatar' }) }
            >
              
            </Item>
            {/* -------------------------------------------------------------- */}
            <Item
              arrow="horizontal"
              extra={userInfo.name}
              onClick={() => setOpenInput({ visible: true, type: 'name' }) }
            >
              
            </Item>
            {/* -------------------------------------------------------------- */}
            <Item
              arrow="horizontal"
              extra={
                <span className={classnames('intro', userInfo.intro ? 'normal' : '')} >
                  {userInfo.intro || ''}
                </span>
              }
              onClick={() => setOpenInput({ visible: true, type: 'intro' })}
            >
              
            </Item>
          </List>


          <List className="profile-list">
            <Item
              arrow="horizontal"
              extra={userInfo.gender === 0 ? '' : ''}
              onClick={() =>setOpenList({visible: true, type: 'gender'})} 
            >
              
            </Item>

{/* DatePicker: https://antd-mobile-v2.surge.sh/components/date-picker-cn/    V2 */}
            <DatePicker
              mode="date"
              value={new Date(userInfo.birthday)}
              title=" //"
              minDate={new Date(1900, 1, 1, 0, 0, 0)}
              maxDate={new Date()}
              onChange={onBirthdayChange}//onChangedatedispatch  redux
            >
              <Item arrow="horizontal" extra={userInfo.birthday}>
                
              </Item>
            </DatePicker>
          </List>

          <input type="file" hidden ref={fileRef} onChange={onUpdateAvatar} />  {/* ------------------/ */}
        </div>

        <div className="logout">
          <button className="btn" onClick={onLogout}>
            
          </button>
        </div>
      </div>
      

      {/*  https://antd-mobile-v2.surge.sh/components/drawer-cn/  */}
      {/* ------ */}
      <Drawer
        className="drawer"
        position="right"//
        style={{ minHeight: document.documentElement.clientHeight }}
        sidebar={//
          openInput.visible && (
            <EditInput
              config={openInputConfig}
              onClose={onCloseOpenInput}
              onCommit={onCommit}                                 //        typevalue
            />
          )
        }
        open={openInput.visible}// | boolean
        onOpenChange={onCloseOpenInput}//open   setOpenInputfalse | open
      >
        {''}
      </Drawer>


      {/* ----- */}
      <Drawer
        className="drawer-list"
        position="bottom"
        sidebar={<EditList config={openListConfig} onClose={onCloseOpenList} />}
        open={openList.visible}
        onOpenChange={onCloseOpenList} 
      >
        {''}
      </Drawer>
    </div>
  )
}

export default ProfileEdit