import Icon from '@/components/Icon'
import Tabs from '@/components/Tabs'
import { getUser, getUserChannel } from '@/store/actions'
import { isAuth } from '@/utils'
import { Drawer } from 'antd-mobile'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ArticleList from './components/ArticleList'
import Channels from './components/Channels'
import MoreAction from './components/MoreAction'
import styles from './index.module.scss'

const CHANNEL_KEY = 'itcast_channel_k'

// rfc
const Home = ({ history }) => {
  const dispatch = useDispatch()
  const tabs = useSelector(state => state.home.userChannel)
  const [tabActiveIndex, setTabActiveIndex] = useState(0)
  const [open, setOpen] = useState(false)

  //  tabs 
  useEffect(() => {
    const loadData = async () => {
      const isLogin = isAuth()

      // 
      if (!isLogin) {
        const localTabs = JSON.parse(localStorage.getItem(CHANNEL_KEY)) || []
        //  tabs 
        if (localTabs.length > 0) {
          dispatch(getUser(localTabs))
          return
        }
      }

      // 1 
      // 2 
      dispatch(getUserChannel())
    }

    loadData()
  }, [dispatch])

  useEffect(() => {
    if (tabs.length === 0) return

    localStorage.setItem(CHANNEL_KEY, JSON.stringify(tabs))
  }, [tabs])

  //  channels
  const onClose = () => setOpen(false)

  //  tabs
  const onChangeTab = value => dispatch(getUser(value))






  return (
    <div className={styles.root}>

      {/*  Tab  */}
      <Tabs
        index={tabActiveIndex}      /* index */
        tabs={tabs} 
        onChange={index => setTabActiveIndex(index)}    /* onChange  */
      >
        {/* ArticleList    */}
        {/* ArticleList activeId={tabs[active].id}  id */}
        {tabs.map(item => (
          <ArticleList key={item.id} channelId={item.id} />  /*  channelId */
        ))}
      </Tabs>

      <div className="tabs-opration">
        <Icon type="iconbtn_search" onClick={() => history.push('/search')} />
        <Icon type="iconbtn_channel" onClick={() => setOpen(true)} />    {/*  Channels  */}
      </div>

      <Drawer
        className="my-drawer"
        style={{ minHeight: document.documentElement.clientHeight }}
        sidebar={
          //  tab   Channels  ''        
          tabs.length > 0 ? (
            <Channels
              tabActiveIndex={tabActiveIndex}
              userChannles={tabs}
              onClose={onClose}
              onChange={onChangeTab}
              onChannelClick={index => setTabActiveIndex(index)}
            />
          ) : (
            ''
          )
        }
        open={open}
        onOpenChange={() => setOpen(false)}
      >
        {''}
      </Drawer>

      {/* more action */}
      <MoreAction />
    </div>
  )
}

