import Input from '@/components/Input'
import NavBar from '@/components/NavBar'
import { login, sendSms } from '@/store/actions'
import { Toast } from 'antd-mobile'
import classnames from 'classnames'
import { useFormik } from 'formik'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { useHistory, useLocation } from 'react-router-dom'
import * as Yup from 'yup'
import styles from './index.module.scss'



/**
 * 
 * 1.onSubmit debugger 
 * 2.dispatch------>return dispatch -> http.get() reduxlocalStorage
 * 3.dispatch
 * 4.formikhandleChangehandleSubmit
 * 5.------->useState---->
 * 6. Input   extra
 * 7.className
 * 8.(history)
 */
const Login = () => {
  const history = useHistory()
  const location = useLocation()
  const dispatch = useDispatch()
  const [time, setTime] = useState(0)

  const formik = useFormik({
    initialValues: {
      mobile: '13900001111',
      code: '246810'
    },
    // 
    validationSchema: Yup.object().shape({
      mobile: Yup.string()
        .required('')
        .matches(/^1[3456789]\d{9}$/, ''),
      code: Yup.string()
        .required('')
        .matches(/^\d{6}$/, '6')
    }),
    // 
    onSubmit: async values => {///--------->   {mobile: '13900001111', code: '246810'}  
      // debugger;
      await dispatch(login(values))
      Toast.info('!', 1)

      // const pathname = location.state ? location.state.from : '/home'
      // history.go(-1): 
      // history.go(1): 
      // history.push(): 
      // history.replace(): 
      
      const { state } = location
      if (!state) {
        history.replace('/home/index')
      } else {
        // 
        history.replace(state.from)
      }
    }
  })


  // 
  const onSendSms = async () => {
    if(time > 0) return
    const { mobile } = formik.values  // formik  
    try {
      // await PromisePromise  --> :MDN
      await dispatch(sendSms(mobile))     //****    -->    //Response
      // Toast.success('', 1)
      Toast.info('!', 1)

      // , 
      setTime(60)
      let timeId =  setInterval(()=> {
        setTime( (time) => {
          if(time === 1 ) { clearInterval(timeId) }
          return time - 1 
        })
      },1000)
    } catch (err){
      // response,  data.message
      // if(err.response){Toast.fail(err.response.data.message, 1)}  
      // else{Toast.fail(',')}
      console.log(err.response.data.message)
    }
  }

  // 
  const onLeftClick = () => history.go(-1)
  const { errors, touched, values, isValid, handleChange, handleSubmit } = formik  //-------formik  ?  touched 


  return (
    <div className={styles.root}>
      <NavBar onLeftClick={onLeftClick} />

      <div className="content">
        <h3></h3>

        <form onSubmit={handleSubmit}>
          <div className="input-item">
            
            {/*  Input  */}
            <Input
              name="mobile"
              value={values.mobile}
              onChange={handleChange}
              placeholder=""
              // onBlur={hanleBlur}    onBlur
              // autoComplete="off"    
            />

            {errors.mobile && touched.mobile && (  <div className="validate">{errors.mobile}</div>    )}
          </div>
          <div className="input-item">
            <Input
              name="code"
              value={values.code}
              onChange={handleChange}
              placeholder=""
              extra={time === 0 ? '' : time + 's' }
              onExtraClick={onSendSms}
              maxLength={6}
            />
            {errors.code && touched.code && (  <div className="validate">{errors.code}</div>  )}
          </div>

          <button
            type="submit"
            disabled={!isValid}
            //  login-btn + disabled , 
            className={classnames('login-btn', !isValid ? 'disabled' : '')}
          >
            
          </button>
        </form>
      </div>
    </div>
  )
}

export default Login
