import { Toast } from 'antd-mobile'
import axios from 'axios'
import { history } from './history'
import { getTokens, removeTokens, setTokens } from './token'

const http = axios.create({
  baseURL: 'http://toutiao.itheima.net/v1_0'   // baseURL: 'http://geek.itheima.net/v1_0'
})

// , 
// 
http.interceptors.request.use(config => {//->config, 
  // console.log(config)///--------->   {url: '/user/channels', method: 'get', headers: {}, baseURL: 'http://toutiao.itheima.net/v1_0', }
  // if (config.method === 'post') { config.headers['Content-Type'] = 'application/json' }
  config.headers['Authorization'] = `Bearer ${getTokens().token}`
  // config.headers.Authorization = 'Bearer'+token
  return config
},(error) => { return Promise.reject(error) } )


// 
http.interceptors.response.use(response => {
  // console.log(response)///--------->   {data: {data: {}, message: 'OK'}, config: {}, status: 200, statusText: 'OK', headers: {}, }
  // debugger;
  return response

}, async error => {//, Promise
  // ==>
  // response
  // response 401
  // response 401 token ( )
  // 401 token token
  //  token

  console.log('error', error.response)
 

  //  
   if (error.response.status === 401) {
    const { token, refresh_token } = getTokens()
    // debugger;

      if (!token || !refresh_token) { //tokenrefresh_token
        history.replace('/login', {
          from: history.location.pathname || '/home'
        })
        return Promise.reject(error)
      }

        try {
          //  token
          const res = await http.put('/authorizations', null, {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
              'Authorization': `Bearer ${refresh_token}`
            }
          })
          console.log('token iS', res)

          //  token    1.redux & localstorage
          setTokens({
            refresh_token,  //---- refresh_token ==== refresh_token: refresh_token
            token: res.data.data.token   //--- axios   data
          })

          //       <token  token >
          return http(error.config)// AxiosPromise  return
          // http  http({url:'', data: { } }})   config

          
        } catch (error) {
          //  refresh_token 
          console.log(error.response)
          console.log(history.location.pathname)

          removeTokens()
          history.push('/login', {
            from: history.location.pathname || '/home'
          })
          Toast.info(' ', 1)
          return Promise.reject(error)
        }
  }

  return Promise.reject(error)
})

export { http }
