import { AuthRoute } from '@/components/AuthRoute'
import Icon from '@/components/Icon'
import { KeepAlive } from '@/components/KeepAlive'
import classnames from 'classnames'
import { Route, useHistory, useLocation } from 'react-router-dom'
import Home from '../Home'
import Profile from '../Profile'
import Question from '../Question'
import Video from '../Video'
import styles from './index.module.scss'

/**
 * 
 * 1.KeepAliveAuthRouteRoute   component/render/children   : component > render > children  URL:https://blog.csdn.net/weixin_44809405/article/details/91393342
 *      : component    KeepAlivechildrend  AuthRouterender
 * 2.  / &  
 *      , pathnameto  to
 * 3. Icon =====> Icon?
 *      Icon Icon return <svg className aria-hidden > <use xlinkHref></use> </svg>
 */


const menus = [
  { id: 1, title: '', to: '/home/index', icon: 'iconbtn_home' },
  { id: 2, title: '', to: '/home/question', icon: 'iconbtn_qa' },
  { id: 3, title: '', to: '/home/video', icon: 'iconbtn_video' },
  { id: 4, title: '', to: '/home/profile', icon: 'iconbtn_mine' }
]

const Layout = () => {
  const history = useHistory()
  const { pathname } = useLocation()///--------->   pathname: /home/question  |  /home/index  |  /home/profile  |   /home/video

  /**
   * 
   * 
   * ,   Suspense 
   *  {   <Swithch>     <Route path="/home/video" component={Video}>   </Route>  </Swithch> }
   * Routepathcomponent
   * path   
   * router  routercomponentrender
   * 
   */ 
  return (
    <div className={styles.root}>
      <KeepAlive alivePath="/home/index" exact path="/home/index" component={Home} />
      <Route path="/home/question" component={Question} />
      <Route path="/home/video" component={Video} />
      <AuthRoute path="/home/profile" component={Profile} />
      {/* react-router 5.0route  =====>component/render/children */}
      {/* <Route path="/home/Profile" component={Profile} />                                                                     */}
      {/* <Route path="/home/Profile" render={() => { <Profile></Profile>}} />    ?  &&  ? render=(props)    */} 
      {/* <Route path="/home/Profile" children={ props => (<div className={props.match? "active": ''}><Link to="path" />	</div>) />        */}


{/*    &  */}
      <div className="tabbar">
        {
          menus.map(item => {
            const isSelected = item.to === pathname
            //  
            // console.log('item iS', item)
            // console.log('isSelected iS', isSelected)
            //  
            return (
              <div
                key={item.id}
                className={classnames('tabbar-item', isSelected ? 'tabbar-item-active' : '' )}   
                onClick={() => history.push(item.to)}
              >
                {/* Icon Icon return <svg className aria-hidden > <use xlinkHref></use> </svg> */}
                <Icon type={`${isSelected ? `${item.icon}_sel` : item.icon}`} />
                <span>{item.title}</span>
              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Layout
