import { http, removeTokens, setTokens } from '@/utils'   //utilsindexjs

const saveToken = tokens => ({ type: 'login/token', payload: tokens })


const sendSms = mobile => {
  // return async (dispatch) => { const res = await request({url: '/sms/mobile'+mobile, method: 'get'})}   300-
  //  dispatch => redux
  return () => {  http.get(`/sms/codes/${mobile}`)  }
}


const login = values => {
  return async dispatch => {
    const res = await http.post('/authorizations', values) ///--------->   {mobile: '13900001111', code: '246810'}
    // console.log(res)///--------->   res.data.data:    refresh_token: "fecbdfeb-4e005f62129a", token: "c4fca56e-fe224785a857"
    //  redux 
    dispatch(saveToken(res.data.data))//(refresh_token, token)
    // -localStorage
    setTokens(res.data.data)
  }
}

const logout = () => {
  return () => {
    removeTokens()
  }
}

export { saveToken }
export { sendSms, login, logout }

