import Input from '@/components/Input'
import NavBar from '@/components/NavBar'
import Textarea from '@/components/Textarea'
import { useState } from 'react'
import styles from './index.module.scss'
// import { useSelector } from 'react-redux'


const EditInput = ({ config, onClose, onCommit }) => {
  // 
  // const defaultValue = useSelector(state => { 
  //   console.log(state.profile.profile[type])
  //   return state.profile.profile[type]
  //  })

  // const config = openInput.type === 'name' ? {title:'',type:'name',value:userInfo.name}: { title:'',type:'intro',value:userInfo.intro }

  const [value, setValue] = useState(config.value || '')

  const { title, type } = config

  const onValueChange = e => {
    setValue(e.target.value)
  }

  return (
    <div className={styles.root}>
      <NavBar
        className="navbar"
        onLeftClick={onClose}
        rightContent={
          <span className="commit-btn" onClick={() => onCommit(type, value)}>
            
          </span>
        }
      >
        {title}
      </NavBar>


      <div className="content">
        <h3>{title}</h3>
        {/* 329-- */}
        {
          type === 'name' ? (
            <div className="input-wrap">
              <Input value={value} onChange={onValueChange} autoFocus />
            </div>
          ) : (
            <Textarea
              placeholder=""
              value={value}
              onChange={onValueChange}
            />
          )
        }
      </div>
    </div>
  )
}

export default EditInput
